<template>
  <div class="student-member-card">
    <div class="student-member-card-avater fl">
      <img :src="item.avater">
    </div>
    <div class="student-member-card-infor fr">
      <div class="student-member-card-infor-name">
        {{item.name}}
        <i
          class="iconfont"
          :class="{'icon-nanxing':item.gender, 'icon-nvxing':!item.gender}"
        ></i>
      </div>
      <div class="student-member-card-infor-number">{{item.number}}</div>
    </div>
    <div class="clear"></div>
  </div>
</template>
<script>
export default {
  props: {
    item: Object
  }
};
</script>
<style lang="less" scoped>
.student-member-card {
  width: 170px;
  border: 1px solid #ebebeb;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 15px;
  margin-right: 15px;
  &-avater {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &-infor {
    text-align: left;
    &-name {
      font-weight: 500;
      .iconfont {
        font-size: 1rem;
      }
      .icon-nvxing {
        color: #ff8181;
      }
      .icon-nanxing {
        color: #2a9efc;
      }
    }
    &-number {
      font-size: 0.8rem;
    }
  }
  &:hover {
    border-color: #2a9efc;
  }
}
</style>



